:root {
    --ease: all 0.5s ease-out;
    --primary-color: #444;
    --secondary-color: #eece1a;
}

 .header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    height: 6em;
    position: relative;
  }

  .header img {
      width: 80px;
      height: 80px;
      border-radius: 50%;
      position: relative;
      top: -16px;
      left: -55px;
  }
   
  .link {
    font-size: 1em;
    border: 3px #fff solid;
    padding: 1em 2.5em;
    margin-bottom: 3rem;
    transition: var(--ease);
    position: relative;
    top: 5px;
  }

  .link:hover {
    padding: 0.8rem 1.9rem;
    background: var(--secondary-color);
    color: var(--secondary-color);


  }
